                <i class="err-ico"></i>
                <span class="tip-font"></span>
                <span class="close-ico"></span>
            </div>
            <div class="normalInput" style="margin-bottom: 20px;">
                <input v-model="username" class="pl_5 inp-focus" type="text" value="" placeholder="/ Flyme " name="account" id="account" maxlength="50" autocomplete="off"><span class="inputTip"></span>
            </div>
            <div class="normalInput">
                <input v-model="password" class="pl_5 inp-focus" type="password" placeholder="" name="password" id="password" maxlength="16" value="" autocomplete="off"><span class="inputTip"></span>
